@import url(https://fonts.googleapis.com/css?family=Roboto+Mono|Ubuntu&display=swap);
p, a, li, button, input, span, i, b {
  font-family: "Roboto Mono", monospace;
  font-size: 1rem; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Ubuntu", sans-serif; }

form {
  display: flex;
  flex-direction: column;
  align-content: stretch; }
  form.hide {
    display: none; }
  form div {
    display: flex;
    flex-direction: column; }

input {
  margin: .25rem 0;
  padding: .8rem; }

button {
  color: #faffff;
  cursor: pointer;
  background-color: #34e7e4;
  margin: 0em;
  padding: 1rem;
  border-width: 0px;
  -webkit-transition: background-color .1s, color .1s;
  transition: background-color .1s, color .1s; }
  button:hover {
    background-color: #00d8d6; }
  button:focus {
    outline: 0; }

.btnAction {
  color: #1e272e;
  background-color: transparent;
  flex-grow: 1; }
  .btnAction:hover {
    background-color: #d2dae2; }
  .btnAction.active {
    background-color: #d2dae2; }

#messages {
  position: absolute;
  top: 0px;
  margin-top: 1rem;
  padding: 1rem 3rem;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  opacity: 0;
  -webkit-transition: opacity .05s ease-in;
  transition: opacity .05s ease-in; }
  #messages p {
    margin: 0;
    padding: 0; }
  #messages.show {
    -webkit-transition: opacity .1s ease-in;
    transition: opacity .1s ease-in;
    opacity: 1; }
  #messages.success {
    background-color: #0be881;
    color: #1e272e; }
  #messages.alert {
    background-color: #ffdd59;
    color: #1e272e; }
  #messages.error {
    background-color: #f53b57;
    color: #faffff; }

html {
  font-size: 100%; }
  html body {
    margin: 0;
    padding: 0; }
    html body .App {
      position: relative;
      width: 100vW;
      height: 100vH;
      display: flex;
      justify-content: center;
      align-content: center;
      align-items: center;
      background: -webkit-linear-gradient(40deg, #575fcf, rgba(255, 0, 0, 0) 70.71%), -webkit-linear-gradient(220deg, #34e7e4, rgba(0, 0, 0, 0) 70.71%);
      background: linear-gradient(50deg, #575fcf, rgba(255, 0, 0, 0) 70.71%), linear-gradient(230deg, #34e7e4, rgba(0, 0, 0, 0) 70.71%); }
      html body .App .userActions {
        background: #faffff;
        position: relative;
        max-width: 400px;
        padding: 1rem; }
        html body .App .userActions .selectors {
          display: flex;
          flex-direction: row;
          align-content: stretch; }
      html body .App .userPage {
        -webkit-transition: opacity 1s;
        transition: opacity 1s;
        background: #faffff;
        position: relative;
        max-width: 500px;
        width: 100%;
        padding: 1rem; }
        html body .App .userPage .logged {
          display: flex;
          flex-direction: row;
          justify-content: space-between; }
          html body .App .userPage .logged button {
            flex-grow: 0; }
        html body .App .userPage .delete button {
          width: 100%; }

